<template>
	<div id="store-manage">
		<public-header title="店铺管理"></public-header>
		<div class="content">
			<div class="wrap-search bg-default">
				<div class="tab-group">
					<div :class="{'tab-item': 1, active: waimaiOrHaodian == 'waimai'}" @click="onToggleTab('waimai')">外卖</div>
					<div :class="{'tab-item': 1, active: waimaiOrHaodian == 'haodian'}" @click="onToggleTab('haodian')">好店</div>
				</div>
			</div>
			<template v-if="waimaiOrHaodian == 'waimai'">
				<template v-if="store.is_waimai == 1">
					<van-cell-group class="margin-10-t">
						<van-cell is-link :to="util.getUrl({path: '/pages/shop/basic', query: {from: 'waimai'}})">
							<i class="icon icon-form c-primary" slot="icon"></i>
							<div slot="title" >基本信息</div>
						</van-cell>
						<van-cell is-link :to="util.getUrl({path: '/pages/shop/qualification', query: {id: id}})">
							<i class="icon icon-shop c-info" slot="icon"></i>
							<div slot="title">营业资质</div>
						</van-cell>
						<van-cell is-link :to="util.getUrl({path: '/pages/shop/update', query: {type: 'notice', id: id}})">
							<i class="icon icon-newshot c-primary" slot="icon"></i>
							<div slot="title">商户公告</div>
						</van-cell>
						<van-cell is-link :to="util.getUrl({path: '/pages/shop/update', query: {type: 'tips', id: id}})">
							<i class="icon icon-information c-info" slot="icon"></i>
							<div slot="title">商品列表页提示</div>
						</van-cell>
					</van-cell-group>
					<van-cell-group class="margin-10-t">
						<van-cell is-link :to="util.getUrl({path: '/pages/shop/account', query: {id: id}})">
							<i class="icon icon-waimai font-20 c-primary" slot="icon"></i>
							<div slot="title" >账户设置</div>
						</van-cell>
						<van-cell is-link :to="util.getUrl({path: '/pages/shop/pill', query: {id: id}})">
							<i class="icon icon-pay c-info" slot="icon"></i>
							<div slot="title" >支付设置</div>
						</van-cell>
					</van-cell-group>
				</template>
				<template v-else>
					<div class="no-data">
						<img src="static/img/tips/message.png" alt="">
						<p>暂无外卖权限，如需开通请联系平台管理员</p>
					</div>
				</template>
			</template>
			<template v-else-if="waimaiOrHaodian == 'haodian'">
				<template v-if="store.is_haodian == 1">
					<van-cell-group class="margin-10-t">
						<van-cell is-link :to="util.getUrl({path: '/pages/shop/basic', query: {from: 'haodian'}})">
							<i class="icon icon-store c-primary" slot="icon"></i>
							<div slot="title" >门店信息</div>
						</van-cell>
					</van-cell-group>
				</template>
				<template v-else>
					<div class="no-data">
						<img src="static/img/tips/message.png" alt="">
						<p>暂无好店权限，如需开通请联系平台管理员</p>
					</div>
				</template>
			</template>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				showPreLoading: true,
				id: 0,
				waimaiOrHaodian: 'waimai',
				store: {
					is_haodian: 0,
					is_waimai: 0,
				}
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: this,
					url: 'manage/shop/index/index',
					success: function(res) {
						that.store = Object.assign(that.store, res.store);
						console.log(that.store)
						that.manager = res.manager;
						that.stat = res.stat;
						that.notice = res.notice;
						//this.order_notice_num = res.order_notice_num;
					}
				});
			},
			onToggleTab(value) {
				if(value == this.waimaiOrHaodian) {
					return;
				} else {
					this.waimaiOrHaodian = value;
				}
			}

		},
		created() {
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#store-manage .content{
		padding-top: 45px;
	}
	#store-manage .van-cell .icon{
		margin-right: 7px;
		font-size: 18px;
	}
</style>


